import { Injectable } from '@angular/core';
import {AuthUser} from "../domain/auth-user";
import {Observable} from "rxjs";
import {HttpClient, HttpHeaders} from "@angular/common/http";

@Injectable({
  providedIn: 'root'
})
export class UserAuthService {

  httpOptions = {
    headers: new HttpHeaders({ 'Content-Type': 'application/json' })
  };

  constructor(private http: HttpClient) { }

  checkUserAuth(UserLogin: AuthUser): Observable<boolean>{
    return this.http.post<boolean>('http://localhost:8080/userlogin-api/auth', UserLogin);
  }

  newUserAuth(UserLogin: AuthUser): Observable<any>{
    console.log(UserLogin);
    let url = `http://localhost:8080/userlogin-api/create/${UserLogin.id}`;

    return this.http.put(url, UserLogin, this.httpOptions);
    // console.log('Got here newUserAuth');
    // console.log(UserLogin);
    // return this.http.put('http://localhost:8080/userlogin-api/create', UserLogin);
  }
}
